// 
import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'

//  @srcwebpack.base.conf.js
import Header from '@/components/Header'
import Sidebar from '@/components/Sidebar'
import compare from '@/components/compare/compare'
const Error404 = resolve => require(['@/components/error/404'], resolve);

// js
const feture = resolve => require(['@/components/feture/feture'], resolve);
const vueRouter = resolve => require(['@/components/vueRouter/vueRouter'], resolve);
const vuex = resolve => require(['@/components/vuex/vuex'], resolve);
const vueVerify = resolve => require(['@/components/vueVerify/vueVerify'], resolve);

// vue-router
Vue.use(Router)

//  
const router = new Router({
  // history
  mode: 'history',
  // 
  routes: [
    // /
    {
      path: '/',
      redirect: '/compare',
      components: {
        default: "",
        sidebar: Sidebar,
        header: Header
      }
    },
    //
    {
      path: '/error/404',
      name: 'error404',
      components: {
        default: Error404,
        sidebar: "",
        header: ""
      },
      meta: {
        module: 'error'
      }
    },
    //Vue
    {
      path: '/compare',
      name: 'compare',
      components: {
        default: compare,
        sidebar: Sidebar,
        header: Header
      },
      meta: {
        module: 'compare'
      }
    },
    //Vue
    {
      path: '/feture',
      name: 'feture',
      components: {
        default: feture,
        sidebar: Sidebar,
        header: Header
      },
      meta: {
        module: 'feture'
      }
    },
    //Vue-router
    {
      path: '/vueRouter',
      name: 'vueRouter',
      components: {
        default: vueRouter,
        sidebar: Sidebar,
        header: Header
      },
      meta: {
        module: 'vueRouter'
      }
    },
    //Vuex
    {
      path: '/vuex',
      name: 'vuex',
      components: {
        default: vuex,
        sidebar: Sidebar,
        header: Header
      },
      meta: {
        module: 'vuex'
      }
    },
    //Vuex
    {
      path: '/vueVerify',
      name: 'vueVerify',
      components: {
        default: vueVerify,
        sidebar: Sidebar,
        header: Header
      },
      meta: {
        module: 'vueVerify'
      }
    },
  ]
})

//hook
//
router.beforeEach((to, from, next) => {
  //  ###########404#############
  // if(to.path !=='/error/404')  {
  //   Vue.http.get('common/routeVerify', {
  //     params: {
  //       path: to.path
  //     },
  //     before(request) {
  //       if (Vue["proviousRequest_routeVerify"]) {
  //         Vue["proviousRequest_routeVerify"].abort();
  //       }
  //       Vue["proviousRequest_routeVerify"] = request;
  //     }
  //   }).then(d => {
  //     alert("");
  //   }, () => {
      
  //     next('/error/404');
  //   })
  // }
  next();
})
// 
export default router;



