import * as types from '../mutation-types'

//
const sider = [
  {
    name: "vue",
    href: "/compare",
    active: false,
    icon: "icon-f-index"
  },
  {
    name: "Vue",
    href: "/feture",
    active: false,
    icon: "icon-f-index"
  },
  {
    name: "Vue-router",
    href: "/vueRouter",
    active: false,
    icon: "icon-f-index"
  },
  {
    name: "Vuex",
    href: "/vuex",
    active: false,
    icon: "icon-f-index"
  },
  {
    name: "Vue",
    href: "/vueVerify",
    active: false,
    icon: "icon-f-index"
  }
]

const state = {
  sider,
  user: {
    name:""
  }
}


// getters
const getters = {
    animationend: state => state.animationend,
    appLoading: state => state.appLoading
}

// actions
const actions = {
  
}

// mutations
const mutations = {
  //false
  setAnimationend (state, type){
    state.animationend = type;
  },  
  setUserName (state, name){
    state.user.name = name;
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



